import React, { ReactElement } from "react"
import { Helmet } from "react-helmet"
import { graphql } from "gatsby"
import { css } from "@emotion/react"
import markdown_github_style_css from "@/markdownStyle/github"
import { TableOfContents } from "@/components/TableOfContents"
import { useLayoutEffect } from "react"
import countingStar from "@/counting_star"
import mq from "@/MediaQuery"

interface node {
  frontmatter: {
    title: string
    tags: string[]
    created: string
  }
  internal: {
    content: string
  }
  html: string
  tableOfContents: string
}

interface edge {
  node: node
}

const template_layout_css = css`
  display: flex;
  /* justify-content: center; */
  /* align-items: center; */
  width: 100vw;
  height: 100vh;
  padding: 3rem 3rem 3rem 3rem;
`

const content_wrapper_css = css`
  display: flex;
  justify-content: center;
  /* align-items: center; */
  ${mq.iPhoneSE} {
    width: 100vw;
  }
  ${mq.iPhoneX} {
    width: 100vw;
  }
  ${mq.MacbookProSmall} {
    width: calc(100vw - 3rem - 260px);
  }
  ${mq.MacbookPro} {
    width: calc(100vw - 3rem - 260px);
  }
`

const content_block_css = css`
  /* display: flex;
  justify-content: center;
  align-items: center; */

  ${mq.iPhoneSE} {
    width: 100vw;
  }
  ${mq.iPhoneX} {
    width: 100vw;
  }
  ${mq.MacbookProSmall} {
    width: calc(100vw - 3rem - 260px);
  }
  ${mq.MacbookPro} {
    width: 50vw;
  }
`

export default function Template({
  data,
  path,
}: {
  data: any
  path: any
}): ReactElement {
  const {
    allMarkdownRemark: { edges },
  }: { allMarkdownRemark: { edges: edge[] } } = data

  useLayoutEffect(() => {
    countingStar()
  }, [])

  const getImageUrl = () => {
    const findImagePhrase: any =
      edges[0].node.internal.content.match(/!\[.*\]\(.*\)/i)
    let firstImagePhrase: any = ""
    if (findImagePhrase !== null)
      firstImagePhrase = findImagePhrase[0]
    if (firstImagePhrase.match(/\(.*\)/i) !== null) {
      firstImagePhrase = firstImagePhrase.match(/\(.*\)/i)[0]
      firstImagePhrase = firstImagePhrase.substring(
        1,
        firstImagePhrase.length - 1
      )
    }
    
    if (typeof window !== "undefined") {
      firstImagePhrase = window.location.origin + firstImagePhrase.slice(1)
    }
    return firstImagePhrase
  }

  return (
    <div css={template_layout_css}>
      {/* <h2>{edges[0].node.frontmatter.created}</h2>
      <h2>{edges[0].node.frontmatter.tags}</h2> */}
      
      <Helmet>
        <title>{edges[0].node.frontmatter.title}</title>
        <meta charSet="utf-8" />
        <meta
          name="viewport"
          content="initial-scale=1.0, maximum-scale=1.0, minimum-scale=1.0, width=device-width, user-scalable=no"
          data-react-helmet="true"
        />
        {/* <meta property="og:description" content="포스트의 설명입니다." /> */}
        <meta property="og:url" content="https://drinkcode.github.io/" />
        <meta property="og:image" content={`${getImageUrl()}`} />
        <meta property="og:image:width" content="1200" />
        <meta property="og:image:height" content="630" />
      </Helmet>
      {/* {[0].map(item => {
        const findImagePhrase: any =
          edges[0].node.internal.content.match(/!\[.*\]\(.*\)/i)
        let firstImagePhrase: any = ""
        if (findImagePhrase !== null)
          firstImagePhrase = findImagePhrase[0]
        if (firstImagePhrase.match(/\(.*\)/i) !== null) {
          firstImagePhrase = firstImagePhrase.match(/\(.*\)/i)[0]
          firstImagePhrase = firstImagePhrase.substring(
            1,
            firstImagePhrase.length - 1
          )
        }
        console.log('firstImagePhrase',firstImagePhrase)
        return firstImagePhrase
      })} */}
      <div css={content_wrapper_css}>
      {edges && (
        <div
          css={[content_block_css, markdown_github_style_css]}
          dangerouslySetInnerHTML={{ __html: edges[0].node.html }}
        />
      )}
      </div>
      <TableOfContents tocData={edges[0].node.tableOfContents} />
    </div>
  )
}

export const pageQuery = graphql`
  query ($title: String) {
    allMarkdownRemark(
      sort: { order: DESC, fields: [frontmatter___created] }
      limit: 1000
      filter: { frontmatter: { title: { eq: $title }, tags: { nin: "temp" } } }
    ) {
      edges {
        node {
          frontmatter {
            title
            tags
            created
            modified
          }
          internal {
            content
          }
          html
          tableOfContents
        }
      }
    }
    allFile(filter: { absolutePath: { regex: "//randomImg/" } }) {
      nodes {
        base
        absolutePath
      }
    }
  }
`

