import React, { ReactElement, useLayoutEffect, useState } from "react"
import { useStaticQuery, graphql, Link } from "gatsby"
import { css } from "@emotion/react"
import mq from "@/MediaQuery"

const toc_wrapper_css = css`
  position: relative;
  width: 260px;
`

const toc_style_css = css`
  /* ${mq.iPhoneX}: {
      display: none;
    } */
  position: absolute;
  padding-left: 20px;
  text-align: left;
  font-size: 14px;
  /* font-weight: 600; */
  width: 240px;
  ul {
    list-style: none;
    padding-left: 10px;
  }
  a {
    color: rgba(136, 136, 136, 255);
    text-decoration: none !important;
  }

  ${mq.iPhoneSE} {
    display: none;
  }
  ${mq.iPhoneX} {
    display: none;
  }
  ${mq.MacbookProSmall} {
    display: block;
  }
  ${mq.MacbookPro} {
    display: block;
  }
`

export function TableOfContents({
  tocData,
}: {
  tocData: string
}): ReactElement {
  const [currentHeaderUrl, setCurrentHeaderUrl] = useState()

  useLayoutEffect(() => {
    window.addEventListener("scroll", handleScroll)
  }, [])

  // useLayoutEffect(() => {
  //     console.log(currentHeaderUrl)
  // }, [currentHeaderUrl])

  const handleScroll = () => {
    let aboveHeaderUrl // 화면 바로 위쪽에 위치한 header
    const HEADER_OFFSET_Y = 10
    const currentOffsetY = window.pageYOffset
    const headerElements: any = document.querySelectorAll(".anchor-header")
    for (const elem of headerElements) {
      const { top } = elem.getBoundingClientRect()
      const elemTop = top + currentOffsetY
      const isLast = elem === headerElements[headerElements.length - 1]
      if (currentOffsetY < elemTop - HEADER_OFFSET_Y) {
        // 기억해둔 aboveHeaderUrl이 있다면 바로 위 header와 현재 element 사이에 화면이 스크롤 되어 있다.
        aboveHeaderUrl &&
          setCurrentHeaderUrl(
            aboveHeaderUrl.split(location.origin + location.pathname)[1]
          )
        // 기억해둔 aboveHeaderUrl이 없다면 첫번째 header다.
        // 이때는 어떤 header도 active 하지 않은 상태다.
        !aboveHeaderUrl && setCurrentHeaderUrl(undefined)
        break
      } else {
        // 마지막 header면 다음 element가 없기 때문에 현재 header를 active header라 간주한다.
        isLast &&
          setCurrentHeaderUrl(
            elem.href.split(location.origin + location.pathname)[1]
          )
        !isLast && (aboveHeaderUrl = elem.href)
      }
    }
  }

  return (
    <div css={toc_wrapper_css}>
      <div
        dangerouslySetInnerHTML={{ __html: tocData }}
        css={[
          toc_style_css,
          css`
            & ul > li a[href*="${currentHeaderUrl}"] {
              color: #333333;
              font-size: 15px;
              font-weight: 600;
            }
          `,
        ]}
      />
    </div>
  )
}
